// import React from 'react';
import { NavBar, Icon, Flex } from 'antd-mobile';
import ShopTab from './ShopTab';

import React, { Component } from 'react'

class Detail extends Component {
    constructor(props){
        super(props);
    }
    componentDidMount(){
        if(localStorage.getItem("name")==null){
            localStorage.setItem("name", this.props.location.params.name);
            localStorage.setItem("path", this.props.location.params.path);
        }
        
    }
    render() {
        const name = localStorage.getItem("name");
        const path = localStorage.getItem("path");
        return (
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}
                >
                    {name}
                </NavBar>
                <img src={require(`${path}`)} style={{ width: 100 + '%' }}></img>
                <Flex style={{ backgroundColor: 'white', position: 'fixed', bottom: 0, width: 100 + '%', paddingLeft: 4 + 'px' }}>
                    <ShopTab />
                </Flex>

            </div>
        )
    }
}

