import React from 'react'
import { Flex } from 'antd-mobile';
import './mappic.css'
import config from './goodsconfig';
const GoodSize = (props) => {
    return (
        <div className="flex-container" className='container' style={{ float: 'left', marginRight: 8 + 'px', marginBottom: 10 + 'px' }}
            onClick={() => 
                props.history.push(
                    {
                        pathname: '/home/detail',
                        params:{
                            name:props.data.name,
                            path:props.data.imgPath
                        }
                    }
                )
            }
        >
            <Flex className='a'  >
                <img src={require(`${props.data.imgPath}`)} className='shopImg' ></img>
            </Flex>
            <Flex className='a'>
                {props.data.name}
            </Flex>
            <Flex style={{ color: 'orange' }} className='a'>
                ￥ {props.data.money}
            </Flex>
        </div>
    )
}
const MapPic = (props) => {
    return <div className="flex-container" style={{ flexWrap: 'wrap', paddingLeft: 15 + 'px', marginTop: 10 + 'px' }}>

        {
            config.map((item) => {
                return <GoodSize data={item} history={props.history} />
            })
        }

    </div>
}
export default MapPic;
